import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { course } from '../course';
import { CourseService } from '../course.service';
import { AuthService } from 'ClientApp/app/auth/auth.service';

@Component({
  selector: 'app-course-edit',
  templateUrl: './course-edit.component.html',
  styleUrls: ['./course-edit.component.css']
})
export class CourseEditComponent implements OnInit {
    editCourse: course;
    errorMsg: string;
    @ViewChild("fileInput") fileInputVariable: any;
    isTeacher: boolean;

    constructor(private route: ActivatedRoute, private router: Router, private CourseService: CourseService, private AuthService: AuthService) { }

    ngOnInit() {
        let id = this.route.snapshot.paramMap.get("id");
        this.CourseService.getCourseById(id).subscribe(
            tcourse => { this.editCourse = tcourse },
            error => { this.errorMsg = <any>error; });
    }

    UpdateCourse(myCourseValues) {
        let fileToUpload = this.fileInputVariable.nativeElement.files[0];
        let formData = new FormData();

        formData.append('id', this.editCourse.id.toString());
        formData.append('Name', myCourseValues.name);
        formData.append('StartDate', myCourseValues.startDate);
        formData.append('Description', myCourseValues.description);
        formData.append('FileData', fileToUpload);

        this.CourseService.EditCourse(formData).subscribe();
        this.router.navigate(['/courses']);
    }

}
